/* ----- colors ----- */

$clashGold: #FFD700;
$darkContent: #464342;
$gray: #F5F5F0;
$lightGray: #E0E0E0;
$lightBrown: #464342;
$lightBlue: #006064;


$elixir: #CC0066;
$dark-elixir: #111;

$primary-color: #9E9E9E;
$secondary-color: #8a6d3b;
$success-color: #3c763d;
$alert: #a94442;


$font-primary: #212121;
$font-secondary: #333333;
$font-light: #727272;

$jumbotron: #eceeef;

$white: #fff;
$black: #000;

$roboto: 'Roboto', sans-serif;

@alias {
	fs: font-size;
	fm: font-family;

	bg: background;
	bgc: background-color;
	bgi: background-image;

	br: border-radius;

	bs:  box-shadow;
}


/*Fonts */
@font-face {
    fm: ClashFont;
    src: url(fonts/supercell-magic-webfont.ttf);
}

.clash-font {
    fm: ClashFont, Courier !important;
    color: $clashGold !important;
}

.roboto {
	fm: $roboto;
}